<template>
    <div class="orderWarp">
        <header id="head_top">
            <section class="head_goback">
                <router-link to="/shop">
                    <i class="el-icon-arrow-left"></i>
                </router-link>
            </section>
            <section class="title_head ">
                <span>确认订单</span>
            </section>
            <router-link to="">
                <i class="el-icon-user-solid" style="font-size: .8rem"></i>
            </router-link>
        </header>
        <router-link to="/chooseAddress" class="address_container">
            <div class="address_empty_left">
                <i class="el-icon-location-outline"></i>
                <div class="address_detail_container">
                    <section class="address_section1">
                        <!--<span>{{addInfo[0].name}}</span>-->
                        <span>{{name}}</span>
                        <span>先生</span>
                        <!--<span>{{addInfo[0].phone}}</span>-->
                        <span>{{phone}}</span>
                    </section>
                    <section class="address_section2">
                        <!--<span style="background-color: rgb(76, 217, 100);">{{addInfo[0].tag}}</span>-->
                        <span style="background-color: rgb(76, 217, 100);">{{tag}}</span>
                        <!--<p>{{addInfo[0].address_detail}}</p>-->
                        <p>{{address}}</p>
                    </section>
                </div>
            </div>
            <i class="el-icon-arrow-right" style="font-size: .8rem"></i>
        </router-link>
        <section class="data">
            <p class="deliver_text">送达时间</p>
            <section class="deliver_time">
                <p>尽快送达 | 预计 {{dingdanArr.delivery_reach_time}}</p>
                <p>蜂鸟专送</p>
            </section>
        </section>
        <section class="pay_way">
            <div class="header_style" @click="fuBtn">
                <span>支付方式</span>
                <div class="more_type">
                    <span>在线支付</span>
                    <i class="el-icon-arrow-right" style="font-size: .6rem"></i>
                </div>
            </div>
            <section class="hongbo">
                <span>红包</span>
                <span>暂时只在饿了么 APP 中支持</span>
            </section>
        </section>
        <section class="food_list">
            <div class="food_list_div1">
                <img src="" alt="">
                <span>叽叽喳喳</span>
            </div>
            <ul class="food_list_ul">
                <li class="food_item_style" v-for="info in carArr" :key="info.id">
                    <p class="food_name ellipsis">{{info.name}}</p>
                    <div class="num_price">
                        <span>x {{info.num}}</span>
                        <span>¥{{info.price}}</span>
                    </div>
                </li>
            </ul>
            <div class="food_item_style">
                <p class="food_name ellipsis">餐盒</p>
                <div class="num_price">
                    <span></span>
                    <span>¥ {{peiPrice}}</span>
                </div>
            </div>
            <div class="food_item_style">
                <p class="food_name ellipsis">配送费</p>
                <div class="num_price">
                    <span></span>
                    <span>¥ {{canPrice}}</span>
                </div>
            </div>
            <div class="food_item_style">
                <p class="food_name ellipsis">订单 ¥{{peiPrice+canPrice+ +allPrice}}</p>
                <div class="num_price">
                    <!--<span></span>-->
                    <span style="color: #f60;">待支付¥{{peiPrice+canPrice+ +allPrice}}</span>
                </div>
            </div>
        </section>
        <section class="pay_way">
            <div class="header_style" @click="bei_zhu_Btn">
                <span>订单备注</span>
                <div class="more_type">
                    <span>口味、偏好等</span>
                    <i class="el-icon-arrow-right" style="font-size: .6rem"></i>
                </div>
            </div>
            <section class="hongbo" @click="hongBtn">
                <span>发票抬头</span>
                <div class="more_type">
                    <span>不需要开发票</span>
                    <i class="el-icon-arrow-right" style="font-size: .6rem"></i>
                </div>
            </section>
        </section>
        <section class="confrim_order">
            <p>待支付 ¥{{peiPrice+canPrice+ +allPrice}}</p>
            <p @click="que_ren_xia_dan">确认下单</p>
        </section>
        <!--//弹出的支付框-->
        <div class="choose_type_Container" v-if="isShowFu">
            <div>支付方式</div>
            <ul>
                <li>
                    <span style="color:#ccc ">
                        货到付款
                        <span>（商家不支持货到付款）</span>
                    </span>
                    <i class="el-icon-circle-check" style="font-size: .8rem;"></i>
                </li>
                <li class="choose">
                    <span style="color: #333">在线支付</span>
                    <i class="el-icon-circle-check" style="font-size: .8rem;color: #4cd964" @click="fu_xuan_Btn"></i>
                </li>
            </ul>

        </div>
    </div>
</template>

<script>
    import { mapState } from "vuex";
    export default {
        name: "ConfiemOrder",
        data(){
            return {
                //弹出的支付框
                isShowFu:false,
                userList:[],
                geohash:'',
                shopCarts:null,
                carArr:[],
                shopId:'',
                entities:[],
                dingdanArr:[],
                peiPrice:'',//餐盒
                canPrice:'',//配送
                allPrice:'',//商品的所有价格
                gou_id:'',//购物车id
                addInfo:[],

                addressID:0,
                name:'',
                phone:'',
                address:'',
                tag:''

            }
        },
        created(){
            this.allPrice = localStorage.getItem("allPrice");

            //店铺的用户名和头像


            //初始化vueX
            this.$store.commit("init_buy_cart");
            this.shopId = localStorage.getItem("shopId")//店铺id
            console.log(this.shopId);

            //收货地址
            this.userId = localStorage.getItem("user_id")
            this.addressID = 0;
            this.addressID = localStorage.getItem('addressID');
            this.axios.get("/v1/users/"+this.userId+"/addresses").then(res=>{
                console.log("hahah",res);
                this.userList = res;
                this.name = this.userList[this.addressID].name;
                this.phone = this.userList[this.addressID].phone;
                this.address = this.userList[this.addressID].address;
                this.tag = this.userList[this.addressID].tag;
                // console.log("hahah",res[0].id);
            }).catch(err=>{});

            // this.addInfo = localStorage.getItem("addressInfo");

            // this.addInfo = JSON.parse(localStorage.getItem("addressInfo"));
            // console.log(this.addInfo)

            this.geohash = localStorage.getItem("geohashId");//经纬度
            // console.log(this.geohash);
            //加入购物车请求
            this.shopCarts = this.$store.state.cartList;//后台拿到的数据
            // console.log(this.shopCarts);
            Object.keys(this.shopCarts).forEach(shopId=>{
                Object.keys(this.shopCarts[shopId]).forEach(category_id => {
                    // console.log(this.shopCarts[shopId]);
                    Object.keys(this.shopCarts[shopId][category_id]).forEach(item_id => {
                        Object.keys(this.shopCarts[shopId][category_id][item_id]).forEach(food_id => {
                            let foodItem = this.shopCarts[shopId][category_id][item_id][food_id];
                            console.log(foodItem);
                            let newFoodItem = {...foodItem, category_id, item_id, food_id,shopId}
                            // this.carArr.push(newFoodItem);
                            if (newFoodItem.shopId == this.shopId) {
                                this.carArr.push(newFoodItem);
                            }
                        });
                    })
                })
            });
            console.log(this.carArr)
                for (let i = 0;i< this.carArr.length;i++){
                    let addressV = [{attrs:[],extra:{},id:this.carArr[i].id,name:this.carArr[i].name,packing_fee:this.carArr[i].packing_fee,
                        price:this.carArr[i].price,quantity:this.carArr[i].num,sku_id:this.carArr[i].sku_id,specs:this.carArr[i].specs,stock:this.carArr[i].stock,}]
                    this.entities.push(addressV);
                }
            console.log(this.entities)
                localStorage.setItem('entitiesArr',JSON.stringify(this.entities))
            // this.$store.state.entitiesArr1("this.entities");
            this.axios.post("/v1/carts/checkout",{
                restaurant_id:this.shopId,
                geohash:this.geohash,
                entities:this.entities,
        }).then(res=>{
            console.log(res)
                this.dingdanArr = res;
            console.log(this.dingdanArr.cart.extra[0].price)
                this.peiPrice = this.dingdanArr.cart.extra[0].price
                console.log(this.dingdanArr.cart.deliver_amount)
                this.canPrice = this.dingdanArr.cart.deliver_amount;
              //购物车id
              //   this.gou_id =res.id;
              //   console.log(this.gou_id)
                localStorage.setItem("gou_wu_che_id",res.id);
                this.$store.state.gou_wu_che_id("res.id")//存到了state里
            }).catch(err=>{})
        },
        methods:{
            fuBtn(){
                this.isShowFu = true;
            },
            fu_xuan_Btn(){
                this.isShowFu = false;
            },
            bei_zhu_Btn(){
                this.$router.push({path:'/remark'})
            },
            hongBtn(){
                this.$router.push({path:'/invoice'})
            },
            que_ren_xia_dan(){
                // this.$store.state.zong_price("this.peiPrice" + "this.canPrice" + +"this.allPrice");
                localStorage.setItem("quan_price",this.peiPrice + this.canPrice + +this.allPrice)
                this.$router.push({path:'/payment'})
            }
        }
    }
</script>

